import React, { useEffect, useState } from 'react';
import RcGantt from 'rc-gantt';

function App() {
  const [tasks, setTasks] = useState([]);

  useEffect(() => {
    //fetch("http://localhost:8080/api/progress/chart")  //開発環境用
    fetch("/api/progress/chart")  //本番環境用
        .then(response => response.json())
        .then(data => {
          const formattedData = data.map(task => ({
          name: task.name, 
          startDate: task.startDate, 
          endDate: task.endDate,
          progress: task.progress,
          collapsed: false
        }));
        
        setTasks(formattedData);
      })
      .catch(error => console.error("Error fetching tasks:", error));
  }, []);

  return (
    <div>
      <div style={{ height: 500 }}>
        <RcGantt
          data={tasks}  // tasksをdataに渡す
          columns={[
            {
              name: 'name',
              label: '名称',
              width: 200,
              maxWidth: 200,
              minWidth: 200,
            },
            {
              name: 'progress',
              label: '進捗率 (%)',
              width: 100,
              maxWidth: 100,
              minWidth: 100,
            }
          ]}

          onUpdate={async () => {
            return true;
          }}
        />
      </div>
    </div>
  );
}

export default App;